header {
    background-color: #000;
    height: 40px;
    padding: 0;
    margin: 0;
}

header .wrap-main {
    height: 40px;
    width: 1200px;
    height: 40px;
    margin: 0 auto;
}
    header .wrap-main nav {
        display: flex;
        justify-content: space-between;
    }
    header .wrap-main nav > li {
        height: 40px;
        width: 150px;
        padding: 0 30px;
        
    }
    header .wrap-main nav > li.logo a{
        line-height: 40px;
        color: #FFF;
        font-size: 22px;
        font-weight: 800;
        letter-spacing: 5px;
        width: 100%;
    }
    .menu-info {
        display: flex;
        }
            header .menu-info * {
                display: inline-block;
                vertical-align: middle;
                color: #fff;
                font-size: 14px;
            }
            .menu-right {
                display: flex;
            }
            .menu-right li {
                line-height: 40px;
                padding: 0 12px;
            }
            .menu-right li:hover {
                background-color: #FFF;
            }
            .menu-right li:hover {
                color: #333;
            }